@use "sass:math";
@use "variables" as *;

nav {
    --nav-link-color: #{$black};
    --nav-scroll-background: #{$white};
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    z-index: 1000000000000000000000000000000000;
    background-color: transparent;
    transition: .3s ease-in-out;

    .row {
        height: 60px;
        align-items: center;
    }

    .site-logo {
        text-align: center;
        text-transform: uppercase;
        letter-spacing: 3px;
        font-family: $font-headline;
        color: var(--nav-link-color);
        padding-left: 63px;
    }

    .nav-link {
        text-align: right;
        text-decoration: none;
        display: inline-block;
        margin-left: auto;
        color: var(--nav-link-color);
    }

    &.dark-nav {
        --nav-link-color: #{$white};
        --nav-scroll-background: #{$black};
        background-color: transparent;
    }

    &.scrolled {
        background-color: var(--nav-scroll-background);
    }

}