@use "variables" as *;

:root {
    --font-color-base: #{$black};
}

body {
    font-family: $font-base;
    font-size: $font-size-base;
    font-weight: $font-base-weight;
    color: var(--font-color-base);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

a {
    color: inherit;
}

.sr-only {
    opacity: 0;
    height: 0;
    font-size: 0;
}

@each $title, $size in $font-sizes {
    h#{$title}, .heading\:#{$title} {
        font-size: #{$size};
        font-family: $heading-font-family;
        font-weight: $heading-font-weight;
        line-height: $heading-line-height;
        color: var(--font-color-base);
    }
}

@each $title, $size in $display-font-sizes {
    .display\:#{$title} {
        font-size: #{$size};
        font-family: $heading-font-family;
        font-weight: $heading-font-weight;
        line-height: $display-line-height;
        margin-bottom: calc(#{$size} * .5);
        color: var(--font-color-base);
    }
}

@each $title, $size in $font-sizes {
    .fontsize\:#{$title} {
        font-size: #{$size};
        line-height: $heading-line-height;
    }
}

@each $title, $font in $fonts {
    .font\:#{$title} {
        font-family: #{$font};
    }
}

@each $title, $value in $theme-colors {
    .color\:#{$title} {
        color: #{$value};
    }
}

@each $transform in $text-transform {
    .textransform\:#{$transform} {
        text-transform: #{$transform};
    }
}

.text\: {
    &left {
        text-align: left;
    }
    &center {
        text-align: center;
    }
    &right {
        text-align: right;
    }
}

[theme-mode="dark"], .background\:black, .background\:teal {
    --font-color-base: #{$white};
}

.label {
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 700;
    font-size: 12px;
}

.label\:description {
    display: inline;
    text-transform: uppercase;
    font-weight: 700;
    text-align: right;
    letter-spacing: 2px;
    font-size: 12px;
    padding-left: $spacer-10;
    position: relative;
    margin-left: auto;
    margin-right: auto;

    &:before {
        content: '';
        height: 2px;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translateY(-50%);
        width: $spacer-8;
        background-color: currentColor;
    }

}

.label\:chip {
    display: inline-block;
    padding: calc($spacer-1 * .75);
    border-radius: calc($spacer-1 * .25);
    border: 1px solid $black;
    font-size: 12px;
    text-transform: uppercase;
    margin-right: $spacer-2;
    margin-bottom: $spacer-2;
}

.list\:chip {
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    list-style: none;
}

.list\:icon {
    list-style: none;
    padding: 0;

    li {
        position: relative;
        padding-left: $spacer-2;

        &:before {
            content: '—';
            color: inherit;
            position: absolute;
            top: 0px;
            left: 0;
        }

    }

}