@use "sass:math";
@use "variables" as *;

.background\: {
    @each $title, $value in $theme-colors {
        &#{$title} {
            background-color: #{$value};
        }

        @each $level in $levels {
            &#{$title}\:#{$level} {
                background-color: rgba($value, math.div($level, 100));
            }
        }    
    }
}
