@use "sass:math";
@use "variables" as *;

:root {
    --work-card-color: #{$black};
}

.work {
    min-height: 100vh;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding-top: 200px;
    padding-bottom: $spacer-3;
    margin-left: auto;
    margin-right: auto;

    @media screen and (max-width: $breakpoint-lg) {
        padding-top: 250px;
    }

    &\:card {
        display: inline;
        margin-right: 15px;
        margin-bottom: 10px;
        text-decoration: none;
        color: var(--work-card-color);
        word-wrap: break-word;
        overflow-wrap: break-word;
        white-space: normal;

        &\:\:label {
            display: inline-flex;
            vertical-align: middle;
            flex-direction: column;
            line-height: .6;
            text-align: center;
            font-size: 80%;
            margin-right: $spacer-1;

            &\:index {
                margin-bottom: calc($spacer-1 * .5);
            }

            &\:type {
                padding: calc($spacer-1 * .75);
                border: 1px solid var(--work-card-color);
                border-radius: calc($spacer-1 * .25);           
            }

        }

        &\:title {
            font-family: $font-headline;
            font-weight: 300;
            font-size: 56px;
            vertical-align: middle;
            word-wrap: break-word;
            overflow-wrap: break-word;
            white-space: normal;  
            color: var(--work-card-color);
            transition: color .5s ease-in-out;

            @media screen and (max-width: $breakpoint-md) {
                font-size: 46px;
            }

        }

        &:hover {
            .work\:card\:title {
                color: $copper;
            }
        }

    }
}

.jumbotron\:casestudy {
    min-height: 100vh;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 1;

    .image\:container {
        position: absolute;
        top: 60px;
        right: 60px;
        bottom: 60px;
        left: 60px;
        z-index: -1;
        overflow: hidden;
        background-color: $black;

        @media screen and (max-width: $breakpoint-md) {
            top: 0;
            right: 0;
            left: 0;
            bottom: 0;
        }

        .image\:featured {
            overflow: hidden;
            width: 100%;
            height: 100%;

            img {
                height: 100%;
                width: 100%;
                object-fit: cover;
                object-position: center;
                opacity: .3;
            }
        }

    }

    .display\:1 {
        text-align: center;
    }
}

.image\:section {
    position: relative;

    @media screen and (min-width: $breakpoint-md) {
        .col-md-4 {
            height: 100%;
            position: sticky;
            top: 120px;
        }
    }

}


[theme-mode="dark"], .background\:black, .background\:teal {
    --work-card-color: #{$white};
}