@use "sass:math";
@use "variables" as *;

@each $breakpoint-title, $breakpoint-value in $grid-breakpoints {
  @if $breakpoint-value > 0 {
    @media screen and (min-width: #{$breakpoint-value}) {
      @each $level in $levels {
        .#{$breakpoint-title}\:min-height\:#{$level}vh {
          min-height: #{$level}vh;
        }
      }
    }
  } @else {
    @each $level in $levels {
      .min-height\:#{$level}vh {
        min-height: #{$level}vh;
      }
    }
  }
}

@each $breakpoint-title, $breakpoint-value in $grid-breakpoints {

    @if $breakpoint-value > 0 {
        @media screen and (min-width: #{$breakpoint-value}) {
            @each $type in $spacer-indentifier {
                @each $spacer-title, $spacer-value in $spacers {
                    // Verticals
                    .#{$breakpoint-title}\:#{$type}-y\:#{$spacer-title} {
                        #{$type}-top: #{$spacer-value}; 
                        #{$type}-bottom: #{$spacer-value}; 
                    }
                    // Horizotnals
                    .#{$breakpoint-title}\:#{$type}-x\:#{$spacer-title} {
                        #{$type}-left: #{$spacer-value}; 
                        #{$type}-right: #{$spacer-value}; 
                    }

                    @each $axis in $axis {
                        .#{$breakpoint-title}\:#{$type}-#{$axis}\:#{$spacer-title} {
                            #{$type}-#{$axis}: #{$spacer-value};  
                        }
                    }


                }
            }
        }
    }@else {
        @each $type in $spacer-indentifier {
            @each $spacer-title, $spacer-value in $spacers {
                // Verticals
                .#{$type}-y\:#{$spacer-title} {
                    #{$type}-top: #{$spacer-value}; 
                    #{$type}-bottom: #{$spacer-value}; 
                }
                // Horizotnals
                .#{$type}-x\:#{$spacer-title} {
                    #{$type}-left: #{$spacer-value}; 
                    #{$type}-right: #{$spacer-value}; 
                }

                @each $axis in $axis {
                    .#{$type}-#{$axis}\:#{$spacer-title} {
                        #{$type}-#{$axis}: #{$spacer-value};  
                    }
                }
            }
        }
    }
}